/**
 * CSS styles used Admin notice
 */

.wrap .st-notice.notice {
    padding: 0;
    margin: 5px 0 10px;
    border: 1px solid #E5E5E5;
    background: #FFF;
    overflow: hidden;
    -webkit-border-radius: 6px;
    border-radius: 6px;
    -webkit-box-shadow: 0 1px 1px 0 rgba(0, 0, 0, 0.05);
    box-shadow: 0 1px 1px 0 rgba(0, 0, 0, 0.05);
    position: relative;
    z-index: 1;
    min-height: 80px;
    display: table; /* The magic ingredient! */
    &.error { 
      border-left: 4px solid red; 
    }


}

.st-animate-attention
{
  @animation_time: 2s;
  -webkit-animation:st_attention @animation_time linear infinite;
  -moz-animation:st_attention @animation_time linear infinite;
  animation:st_attention @animation_time linear infinite;
}
@keyframes st_attention {  
  0% { box-shadow: 0 0 0 0px fade(#ff0000, 0%); }
  50% { box-shadow: 0 0 0 5px fade(#ff0000, 20%); }
  100% { box-shadow: 0 0 0 0px fade(#ff0000, 10%); }
}


.st-notice > div {
    display: table-cell; /* The magic ingredient! */
    vertical-align: middle;
    cursor: default;
    line-height: 1.5;
}

.st-notice-logo {
    padding: 3px 0 0 20px;
}

.st-notice-logo span {
    display: block;
    width: 50px;
}

.st-notice-message {
    width: 100%;
    padding: 10px 20px;
    color: #444;
}

.st-notice-message strong {
    color: #000;
}

.st-notice-cta {
    border-left: 1px solid #E5E5E5;
    background: #F8F8F8;
    padding: 0 30px;
    position: relative;
    white-space: nowrap;
}

.wp-core-ui .st-notice-cta button,
.wp-core-ui .st-notice-cta .button-primary:active {
    vertical-align: middle;
}

.st-notice-dismiss {
    background: transparent;
    border: 0;
    cursor: pointer;
    color: #BBB;
}

.st-notice-dismiss:hover {
    color: #666;
}