.toplevel_page_cbt_options.admin-color-fresh .redux-container #redux-header {
/*
  background: #AABFC1;
  background-image: -webkit-linear-gradient(top, #103875 0%, #142D52 100%);
  background-image: linear-gradient(to bottom, #103875 0%, #142D52 100%);
  background-repeat: repeat-x;
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#ff103875', endColorstr='#ff142D52', GradientType=0);
*/
  background: #fff url('../images/brand-line.png') bottom center repeat-x;
  background-size: 100% 2px;
  border: none;
	
  h2 img { margin: -13px 0 -15px; }

  .display_header
  {
    margin: 0 auto;
    float: none;
    text-align: center;
  }
  .cbt_theme_logo { 
    width: 200px; 
    position: relative;
    @animation_time: 9s;
    svg .ring1 {
      transform-origin: 50% 50%;
      -webkit-animation:logo_spin @animation_time linear infinite;
      -moz-animation:logo_spin @animation_time linear infinite;
      animation:logo_spin @animation_time linear infinite;
    }

    svg .ring2 {
      transform-origin: 50% 50%;
      -webkit-animation:logo_spin_reverse @animation_time linear infinite;
      -moz-animation:logo_spin_reverse @animation_time linear infinite;
      animation:logo_spin_reverse @animation_time linear infinite;
    }

  }
}

.cbt_logo_extension_label { display: none; }
body.cbt_takeaway_enabled
{
  #redux-header 
  {
    background-color: #1b1f44 !important;
    .cbt_theme_logo
    {
      margin-right: 40px;
      svg { fill: #fff; }
      i.cbt_logo_extension_label
      {
        display: block;
        color: #f7a735;
        position: absolute;
        top: 24%;
        right: -89px;
        font-style: normal;
        font-weight: 100;
        border-left: 1px solid;
        padding: 4px 0 4px 9px;
        font-size: 0.95em;
      }
    }

  }
}

@-moz-keyframes logo_spin { 100% { -moz-transform: rotate(360deg); } }
@-webkit-keyframes logo_spin { 100% { -webkit-transform: rotate(360deg); } }
@keyframes logo_spin { 100% { -webkit-transform: rotate(360deg); transform:rotate(360deg); } }

@-moz-keyframes logo_spin_reverse { 100% { -moz-transform: rotate(-360deg); } }
@-webkit-keyframes logo_spin_reverse { 100% { -webkit-transform: rotate(-360deg); } }
@keyframes logo_spin_reverse { 100% { -webkit-transform: rotate(-360deg); transform:rotate(-360deg); } }


