body.toplevel_page_cbt_options
{
  .button-primary {
    @color: @brand-color-3;

    background: @color;
    border-color: lighten(@color, 5%) darken(@color, 10%) darken(@color, 10%);
    -webkit-box-shadow: 0 1px 0 darken(@color, 10%);
    box-shadow: 0 1px 0 darken(@color, 10%);
    color: #fff;
    text-decoration: none;
    text-shadow: 0 0px 3px rgba(0, 0, 0, 0.5);

    &.button-green
    {
      @color: #00B204;
      background: @color;
      border-color: lighten(@color, 5%) darken(@color, 10%) darken(@color, 10%);
      -webkit-box-shadow: 0 1px 0 darken(@color, 10%);
      box-shadow: 0 1px 0 darken(@color, 10%);
    }

    &:hover
    {
      @color: @brand-color-1;

      background: @color;
      border-color: lighten(@color, 5%) darken(@color, 10%) darken(@color, 10%);
      -webkit-box-shadow: 0 1px 0 darken(@color, 10%);
      box-shadow: 0 1px 0 darken(@color, 10%);
    }
  }

  li.st-new-tag > a
  {
    position: relative;
    overflow: hidden;
    &::before
    {
      content: 'NEW';
      background: @brand-color-2;
      z-index: 1;
      width: 54px;
      font-size: 9.5px;
      position: absolute;
      top: 6px;
      left: -15px;
      transform: rotate(-45deg);
      text-align: center;
      line-height: 1.26em;
      box-shadow: 0 0 6px 0px rgba(0, 0, 0, 0.25);
      border: 1px solid lighten(@brand-color-2, 30%);
    }
  }


}



