#cbt_overlay {
  width: 100%;
  height: 100%;
  background: #000;
  z-index: 1000;
  background: url('../images/overlay_background.png');
  top: 0;
  left: 0;
  position: fixed;
  display: none;
  z-index: 99999;
  overflow: scroll;

  &::after
  {
    content: "\f057";
    font: normal normal normal 55px/1 FontAwesome;
    position: absolute;
    right: 20px;
    top: 50px;
    color: #fff;
    opacity: 0.4;    
  }
}
#cbt_overlay > #cbt_popup {
  width: 800px;
  min-height: 30px;
  padding: 15px 15px;
  margin: auto;
  position: absolute;
  top: 100px;
  z-index: 1001;
  background: #fff;
  left: 50%;
  margin-left: -430px;
  // max-height: 600px;
  overflow: auto;
  box-shadow: 0px 1px 35px rgba(0, 0, 0, 0.5);
}
.cbt_loader {
  height: 27px;
  background: transparent url('../images/loading-bar.gif') center no-repeat;
}
