  border: 1px solid transparent;
  border-radius: 5px;
  padding: 5px;
}
#cbt_colors ul.cbt_swatches li.cbt_swatch:hover {
  background: #000;
  color: #fff;
}
#cbt_colors ul.cbt_swatches li.cbt_swatch li {
