// ------------------------------------------------------
//  Unbranding
// ------------------------------------------------------
body.cbt-unbranded
{
	// themes page
	&.themes-php .theme-browser .themes .theme.active {
		.theme-name { text-indent: -9999px; }
		.theme-screenshot { background: #2F2F2F; img{display:none;} }
	}
	&.themes-php .theme-overlay.active
	{
		.theme-screenshots { background: #2F2F2F; img{display:none;} }
		.theme-name { 
			text-indent: -9999px; 
			position: relative;
			.theme-version
			{
			    text-indent: 0;
				position: absolute;
				left: 0;
			}
		}
		.theme-author, .theme-description { display: none; }
	}
	
	// customizer unbranding
	#customize-info, #accordion-section-themes { display: none !important; }
	
	// page builder
	#wr_page_builder #branding .pull-right { display: none; }
	
	// plugins page
	tr#prostyler-builder .plugin-version-author-uri a { display: none; }
	
	// options panel
	#cbt_options-cbt_theme_info, 
	#redux-share { display: none !important; }
}

